
export default {
  name: 'localeTextFunc',
  localeTextFunc (key, defaultValue) {
    let localeTextMap = {
      // 
      loadingOoo: '',
      noRowsToShow: '',

      // 
      filterOoo: '',
      equals: '=',
      notEqual: '!=',
      startsWith: '^()',
      endsWith: '$()',
      contains: '*()',
      notContains: '!()',
      clearFilter: '',
      applyFilter: '',

      // 
      page: '',
      to: '-',
      of: '',
      first: '',
      previous: '',
      next: '',
      last: '',
      more: ''
    }

    let localeText = localeTextMap[key]
    if (localeText) {
      return localeText
    } else {
      console && console.warn(key + ': \'' + defaultValue + '\',')
      return defaultValue
    }
  }
}



